<template>
  <div>
    <header
      style="
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        background: rgba(255, 255, 255, 0.8);
        z-index: 1000;
      "
    >
      <div class="menus">
        <ul v-show="showMenu">
          <li><input type="checkbox" v-model="showEdit" /></li>
          <li @click="load">Reload</li>
          <li><router-link to="/test"></router-link></li>
          <li><router-link to="/edit"></router-link></li>
          <li @click="cmd({ cmd: 'detach', val: !detach })">
            {{ detach ? "Attach" : "Detach" }}
          </li>
        </ul>
        <div>
          <font-awesome-icon
            style="width: 100%"
            :icon="showMenu ? 'angle-up' : 'angle-down'"
            fixed-width
            @click="showMenu = !showMenu"
          />
        </div>
      </div>

      <h4 style="margin: 5px"></h4>

      <div class="tabs" style="clear: both">
        <ul>
          <li
            class="tab"
            v-for="(tab, i) in tabs"
            @click="curTabIndex = i"
            :key="i"
            :class="{ curTab: curTabIndex == i }"
          >
            {{ tab.name }}
          </li>
        </ul>
      </div>
    </header>

    <div style="margin-top: 110px">
      <div class="swiper-box">
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div
              class="swiper-slide"
              v-for="(item, index) in tabs"
              :key="index"
            >
              <keep-alive>
                <component :is="item.comp"></component>
              </keep-alive>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script >
import ByVideo from "./ByVideo";
import ByImage from "./ByImage";
import ByAudio from "./ByAudio";
import ByWhen from "./ByWhen";
import EditWords from "./EditWords";
import Swiper from "swiper";
import "swiper/swiper-bundle.css";
var mySwiper;
export default {
  components: {
    ByVideo,
    ByAudio,
    ByImage,
    ByWhen,
    EditWords,
  },
  data() {
    return {
      showTest: false,
      curTabIndex: 0,
      showMenu: 0,
      tabs: [
        { name: "", comp: "ByVideo" },
        { name: "", comp: "ByImage" },
        { name: "", comp: "ByAudio" },
      ],
    };
  },
  mounted() {
    mySwiper = new Swiper(".swiper-container", {
      initialSlide: 0,
    });
    mySwiper.on("slideChange", () => {
      // 
      // slidenav
      this.curTabIndex = mySwiper.activeIndex;
      //this.$root.eventHub.$emit("slideTab", mySwiper.activeIndex);
    });
    // nav
    /*this.$root.eventHub.$on("changeTab", (index) => {
      // 
      mySwiper.slideTo(index, 0, false);
    });
    console.log(mySwiper);*/

    this.$nextTick(() => {
      document.addEventListener("scroll", this.handleScroll, true);
    });
  },

  computed: {
    detach() {
      return this.$store.state.playController.detach;
    },
    showEdit: {
      get() {
        return this.$store.state.showEdit;
      },
      set(value) {
        this.$store.commit("showEdit", value);
      },
    },
  },
  methods: {
    cmd(params) {
      this.$store.dispatch("cmdAction", params);
    },
    handleScroll() {
      var scrollTop = 0;
      if (document.documentElement && document.documentElement.scrollTop) {
        scrollTop = document.documentElement.scrollTop;
      } else if (document.body) {
        scrollTop = document.body.scrollTop;
      }

      var clientHeight = 0;
      if (document.body.clientHeight && document.documentElement.clientHeight) {
        clientHeight = Math.min(
          document.body.clientHeight,
          document.documentElement.clientHeight
        );
      } else {
        clientHeight = Math.max(
          document.body.clientHeight,
          document.documentElement.clientHeight
        );
      }

      var scrollHeight = Math.max(
        document.body.scrollHeight,
        document.documentElement.scrollHeight
      );

      if (scrollTop + clientHeight == scrollHeight) {
        console.log("bottom");
      }
    },
    load() {
      fetch("/api/reLoadPlayList", {
        method: "get",
      }).then((data) => {});
    },
  },
  watch: {
    curTabIndex() {
      if (
        ["ByWhen", "EditWords"].indexOf(this.tabs[this.curTabIndex].comp) > -1
      ) {
        this.$store.commit("showPlayer", false);
      } else this.$store.commit("showPlayer", true);
      $("html, body").animate({ scrollTop: 0 }, 0);
      mySwiper.slideTo(this.curTabIndex, 0, false);
    },
  },
};
</script>

