.tab {
  cursor: pointer;
}
.curTab {
  background: white;
}

.curTab:after {
  content: "";
  width: auto;
  min-width: 44px;
  height: 2px;
  background: #4e6ef2;
  border-radius: 1px;
  display: block;
  margin-top: 1px;
}
ul {
  list-style-type: none;
  padding: 0;
}
.tabs li {
  display: inline-block;
  margin: 0 10px;
}
.menus ul {
  padding: 0;
  margin: 0;
}
.menus li {
  background: gray;
  color: white;
  cursor: pointer;
}
.menus a {
  background: gray;
  color: white;
}
</style>
